@mixin sizing-percent .height, height;
@mixin sizing-vh .vh\:height, height;
@mixin sizing-pixel .px\:height, height;
@mixin sizing-em .em\:height, height;
@mixin sizing-mixin .rem\:height, height;

@media (min-height: 640px) {
  @mixin sizing-percent .small\:height, height;
  @mixin sizing-vh .small\:vh\:height, height;
  @mixin sizing-pixel .small\:px\:height, height;
  @mixin sizing-em .small\:em\:height, height;
  @mixin sizing-mixin .small\:rem\:height, height;
}

@media (min-height: 768px) {
  @mixin sizing-percent .medium\:height, height;
  @mixin sizing-vh .medium\:vh\:height, height;
  @mixin sizing-pixel .medium\:px\:height, height;
  @mixin sizing-em .medium\:em\:height, height;
  @mixin sizing-mixin .medium\:rem\:height, height;
}

@media (min-height: 1024px) {
  @mixin sizing-percent .large\:height, height;
  @mixin sizing-vh .large\:vh\:height, height;
  @mixin sizing-pixel .large\:px\:height, height;
  @mixin sizing-em .large\:em\:height, height;
  @mixin sizing-mixin .large\:rem\:height, height;
}

@media (min-height: 1280px) {
  @mixin sizing-percent .larger\:height, height;
  @mixin sizing-vh .larger\:vh\:height, height;
  @mixin sizing-pixel .larger\:px\:height, height;
  @mixin sizing-em .larger\:em\:height, height;
  @mixin sizing-mixin .larger\:rem\:height, height;
}

@media (min-height: 1536px) {
  @mixin sizing-percent .largest\:height, height;
  @mixin sizing-vh .largest\:vh\:height, height;
  @mixin sizing-pixel .largest\:px\:height, height;
  @mixin sizing-em .largest\:em\:height, height;
  @mixin sizing-mixin .largest\:rem\:height, height;
}
